//useState    . ,    , set    .
import React, {useState} from 'react';
//styled-components css     .
import styled from 'styled-components';
import image from '../../img/mainpage.png';
import imagecircle from '../../img/circle.png';
//react-router-dom      .
//    ,    .
import { useHistory } from "react-router-dom";

function Home(props) {
    //useState    . ,    , set    . 
    // setWidth  Img  width  ,   43vw 90vw .
    const [width, setWidth] = useState('43vw');
    //setHeight  Img  height  ,   43vw 90vw .
    const [height, setHeight] = useState('43vw');
    //setMarginTop  Img  margin-top  ,   -53vw -80vw .
    const [marginTop, setMarginTop] = useState('-53vw');
    //setColor  Text  color  ,   #F5F8FF #161616 .
    const [color, setColor] = useState('#F5F8FF');

    //useHistory     .
    //, history.replace    , window.location.reload()   .
    const history = useHistory();

    function handler2() {
        //  history.replace("/club");   /club .
        history.replace("/homeintent");
        //window.location.reload()  .
        window.location.reload();
    }
    return (
        <Container>
            <Img src ={image}></Img>
            <Img2 src ={imagecircle} className='img2' style={{width, height,marginTop}} onClick={() => {
                    setWidth('90vw');
                    setHeight('90vw');
                    setMarginTop('-80vw');
                    setColor('#000');
                    //setTimeout       1.25  handler2()   .
                    setTimeout(() => {
                        handler2();
                    }, 1250);
                }}></Img2>
            <Text style={{color}}>MIRIM ALBUM</Text>
        </Container>
    )
}
const Container = styled.div`
  top: 0;
  width: 100vw;
  overflow: hidden;
  height: 100vh;
  background-color: #161616;
`;
const Img = styled.img`
    margin: 0 auto;
    width: 63vw;
    display: flex;
    justify-content: center;
    margin-top: -3vw;
    
    @media only screen and (max-width: 604px) {
        margin-top: 60vw;
        width: 80%;
        height: 41%;
    }`
    ;
const Img2 = styled.img`
    display: flex;
    justify-content: center;
    margin: 0 auto;
    transition: all ease 2s 0s;
    
    @media only screen and (max-width: 604px) {
        margin-top: -62vw !important;
        
    }
    `;
const Text = styled.div`
    font-size: 4.5vw;
    letter-spacing: 0.65rem;
    font-weight: bold;
    font-family: 'Noto Sans KR', sans-serif; 
    display: flex;
    justify-content: center;
    margin: 0 auto;
    margin-top: -25vw; 
    transition: all ease 2s 0s; 
    
    @media only screen and (max-width: 604px) {
        margin-top: -80vw;
        font-size: 9vw;
        width: 100%;
    }`
;
export default Home;